::-webkit-scrollbar {
  width: 10px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2f2f2f; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #fb607f; 
  border-radius: 5px;
}