:root {
    --navbarHeight: 100px;
    --desktopOffset: 10vw;
    --sidebarWidth: 30vw;
    --aboutusOffset: 70vh;
    --red: #fb607f;
    
}

html, body {
    height: 100%;
    width: 100%;
    background-color: black;
    color: white;
    margin: 0px;
    overflow-x: hidden;
}

.titlemob{
    padding: 30vh 0 8vh 5vw;
    font-size: 15vw;
    line-height: 0.8;
    text-align: left;
}

.month {
    font-size: 200%;
    margin-top: 0;
}

#navbar {
    position: fixed;
    height: var(--navbarHeight);
    width: 100vw;

    background-color: black;

    justify-content: space-between;
    align-items: center;

    /* gap: clamp(1rem, 2rem, 3rem); */
    font-size: 15px;

    border-bottom: 1px solid white;
    z-index: 100;
}

.linksContainer {
    height: var(--navbarHeight);
    width: 98%;
    left: 1%;

    position: fixed;

    justify-content: center;
    align-items: center;
}

.about-people {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border: solid white;
    border-width: 1px 0 1px 0;
}

.links {
    height: var(--navbarHeight);
    width: 90%;
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 2vw;
}

.fr {
    display: flex;
    flex-direction: row;
}
.fillspace{
    width: 100%;
    justify-content: space-evenly;
    padding: 0 10% 2% 10%;
}
.fc {
    display: flex;
    flex-direction: column;
}
.fc a{
    text-align: center;
}



#desktop {
    height: 100%;
    width: calc(100vw - 2 * var(--desktopOffset));

    /* border: 1px solid white;
    border-top: 0px;
    border-bottom: 0px; */
    left: calc(var(--desktopOffset));

    position: absolute;
}

#sideBar {

    height: calc(100vh - var(--navbarHeight));
    width: var(--sidebarWidth);
    top: var(--navbarHeight);

    left: var(--desktopOffset);

    justify-content: space-between;

    /* background-color: orange; */
    z-index: 2;

    position: fixed;
    border-right: 1px solid white;
    border-left: 1px solid white;
}

.titleWrapper {
    height: 80vh;
    width: var(--sidebarWidth);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

#margin{
    top: var(--aboutusOffset);
}

.aboutUs {
    height: calc(100% - var(--navbarHeight));
    width: calc(100vw - var(--sidebarWidth) - 2 * var(--desktopOffset));

    /* border-right: 1px solid white; */
    scroll-snap-type: y mandatory;

    position: absolute;
    left: calc(var(--sidebarWidth) + var(--desktopOffset));

    top: 150px;
    color: white;
    /* background-color: red; */
}

.aboutUsSection {
    width: 100%;
    border-bottom: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1px;
    scroll-snap-align: start;
}

.sponimg{
    width: 200px;
}
.sponimgbig{
    width: 400px;
}

.about-social{
    padding: 4%;
}

.vline {
    width: 1px;
    height: 100vh;
    background-color: white;

    position: fixed;
    left: calc(100vw - var(--desktopOffset));
}

.title {
    height: 90%;

}

.three {
    height: 100vh;
    width: 100vw;
    position: fixed;
    background-color: #07000e;
    opacity: 0.8;
}

a {
    color: white;
    font-style: normal;
    text-decoration: none;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.red {
    color: var(--red);
}

.sign {
    width: 100px;
}

.fel {
    font-size: 60px;
}

.forward{
    transform: translateZ(1px);
}

.sign, .links li{
    transition: 0.2s ease;
}
.sign:hover, .links li:hover{
    transform: scale(1.1);
}

.register {
    background-color: transparent;
    color: white;
    border: 1px solid white;
    border-left: none;
    border-right: none;
    font-weight: bold;
    font-size: 150%;
    height: 10vh;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.register:hover {
    background-color: white;
    opacity: 90%;
    color: var(--red);
    transition: all 0.2s ease-in-out;

}

.ver {
    color: white;
    font-size: 150%;
    font-weight: bold;
    margin-bottom: -5%;
}

.triangle {
    width: calc(10% - 1.6px);
    padding: 2% 5% 2% 5%;
    border: solid;
    border-width: 0 1px 0 1px;
}

.ar {
    align-items: end;
}

.aboutUsText {
    /* margin-top: 20px; */
    /* margin-bottom: 20px; */
    margin: 0px;
    width: 80%;
    font-size: 140%;
    padding: 10% 10% 10% 10%;
}
.aboutUsText code{
    font-size: 130%;
}

.aboutUsBlock {
    text-align: center;
    margin: 20px;
}

.white {
    background-color: white;
}

.data {
    height: 45vmin;
    z-index: 4;
    border: solid;
    border-width: 1px 0 1px 0;
}

.dataWrapper_l {
    width: 60%;
    height: 45vmin;
}

.data_lt {
    height: 40vmin;
    width: 100%;
}
.data_lt div{
    padding-left: 10PX;
    border: solid;
    border-width: 0 0 1px 0;
    font-size: 3vh;
    height: calc(5vh - 1px);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.data_lb {
    padding-left: 5%;
    height: 5vmin;
    width: 100%;
    font-size: 200%;
    margin: auto;
}

.data_r{
    width: 50%;
    padding: 1vmin;
    height: 43vmin;
    border: solid;
    border-width: 0 0 0 1px;
}
.prize{
    font-size: 500%;
    margin: 0;
}
.prize h1{
    font-size: 180%;
    margin: -50px 0 -40px 0;
}
.date {
    font-size: 10vmin;
    margin: 0px;
}

.footer {
    background-color: black;
    color: #777;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 25px;
    /* height: 80px; */
}
.footer div{
    width: 30%;
    text-align: center;
}

.blur {
    backdrop-filter: blur(2px);
}

.hamburger-list {
    overflow: hidden;
    background-color: #171717;
    position: fixed;
    top: var(--navbarHeight);
    width: 100%;
    z-index: 99;
}

.hamburger-list #hamburger-list-items {
    z-index: 99;
    display: none;
    height: 0;
    transition: height 3s linear 1s, display 1s;
}

.hamburger-list a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
}
  
.hamburger-list a.icon {
    background: black;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
}

  
.hamburger-list a:hover {
    background-color: #ddd;
    color: black;
}

/* To align sligshot games with SBI logo */
/* .aboutUsSection > div:nth-child(2) > a:nth-child(1){
    margin-top: 3%; 
} */

.hamburger {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    padding: 1.5rem;
    z-index: 999;
    width: 8vw;
  }

  .hamburger > i {
    font-size: 4vh;
  }
  
#date{
    border: solid;
    border-width: 0 0 1px 0;
}

.people{
    scroll-behavior: smooth;
    scrollbar-width: thin !important;
    overflow-x: auto;
    justify-content: flex-start !important;
    padding-bottom: 10%;
}

.people-container{
    padding: 8%;
    /* height: 30vh; */
}

.people > .testimotionals > .card > .content{
    display: block;
    width: 100%;
    font-size: 30px;
}

.testimotionals {
    width:400px;
    display:inline-block;
    margin-left:50px;
}
    
.testimotionals .card {
    position:relative;
    overflow:hidden;
    height: 250px;
    width:350px;
    margin:0 auto;
    background:#171717;
    padding:60px 20px 20px 20px;
    box-sizing:border-box;
    text-align:center;
    box-shadow:0 10px 40px rgba(0,0,0,.5)
}
.testimotionals .card .layer {
    z-index:1;
    position:absolute;
    top:calc(100% - 2px);
    height:100%;
    width:100%;
    left:0;
    background:linear-gradient(to left , #e14897, #fb607f);
    transition:0.5s;
    
}
  
.testimotionals .card .content {
    z-index:2; 
    position:relative;
}
  
.testimotionals .card:hover  .layer{
    top:0;
}
  
.testimotionals .card .content p {
    font-size:14px;
    line-height:24px;
    color:#fff;
    
}
.testimotionals .card .content .image {
    width:100px; height:100px;
    margin:0 auto;
    border-radius:50%;
    overflow:hidden;
    border: 4px solid white;
    box-shadow: 0 10px 40px rgba(0,0,0,0.5);
    
}
  
.testimotionals .card .conte nt .details h2 {
  font-size:18px;
  color:white;
}
.testimotionals .card .content .details h2 span {
  font-size:18px;
  color:purple;
    transition:0.5s;
}
.testimotionals .card:hover .content .details h2 span {
    color:white;
    position:relative
}
.details {
    display: block !important;
    font-size: 60% !important;
  }

@media (pointer: none), (pointer: coarse) {
    .aboutUs {
        width: 100vw;
        position: relative;
        left: 0px;
    }
    .cmng{
        backdrop-filter: blur(1px);
    }

    body {
        overflow-x: hidden;
    }
    .white{
        background-color: white;
    }
    .footer{
        border-width: 1px 0 0 0;
    }
    /* Mobile hamburger */
  .hamburger {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    padding: 1.5rem;
    z-index: 999;
    width: 8vw;
  }

  .hamburger > i {
    font-size: 4vh;
  }
  .navbar > .links, .sign {
    display: none;
  }

  .links {
    display: none;
  }
  .about-people {
    position: relative;
    margin-left: 0;
    margin-right: 0;
    padding: 5%;
  }

  .about-social{
    width: 12%;
    padding: 5%;
   }

   /* To align sligshot games with SBI logo on mobile*/
   /* .aboutUsSection > div:nth-child(2) > a:nth-child(1){
        margin-top: 7%;
   } */

  .about-people > div {
    padding: 1%;
  }
  .testimotionals {
    width: 400px;
    height: 215px;
    margin-left: 20px;
  }

  .footer div{
    width: 80%;
   }

  ::-webkit-scrollbar {
    width: 0px;
  }
  .data{
    display: flex;
    flex-direction: column;
    height: 210vmin;
    text-align: center;
    border: none;
    margin: auto;
  }
  .dataWrapper_l{
    height: 100vmin;
    width: 100vmin;
    font-size: 5px;
    text-align: center;
  }  
  .data_r{
    width: 100vmin;
    height: 95vmin;
    margin-top: 5%;
  }
  .data_lb{
    margin: 0;
    font-size: 500%;
  }
  .data_lt{
    height: 70vmin;
  }
  .wrapmob{
    font-size: 150%;
    width: 100%;
  }
  .wrapmob div{
    border: solid;
    padding-top: 3%;
    padding-bottom: 3%;
    border-width: 1px 0 1px 0;
  }
}